import React, { useEffect, useState } from "react";
const StatusWithHook = (props) => {
    
    const [status,setStatus] = useState(props.status);
    const [actived,editActived] = useState(true);
    useEffect(() => {
        setStatus(props.status)
    }, [props.status]);

    const activateonChange = (e)=>{
debugger
             e? editActived(true) : editActived(false)

         
            if(actived!==true & status!==''){
              props.updateStatus(status)
            }  
          }

    const onStatusChange = (e) => {
            setStatus(e.target.value)
          }
          
    return(
        
    <>
     <div >
        {actived ?
           <span onDoubleClick={()=>{activateonChange(false)}}>{props.status || "--------"}</span>:
          <input autoFocus={true} onBlur={()=>{activateonChange(true)}} onChange={onStatusChange} value={status}/>
                }
             </div>
            </>
        ) 
}


export default StatusWithHook