import { stopSubmit } from "redux-form";
import { authAPI } from "../api/api";

const SET_USER_DATA = "SET_USER_DATA";
let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false
};

const authReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        default:
            return state;
    }
};
export default authReducer;
export const setUserData = (id, email, login, isAuth) => {

    return {
        type: SET_USER_DATA,
        payload: {
            id,
            email,
            login,
            isAuth
        }
    };
};
export const geAuthUserData = () => (dispatch) => {
    return authAPI.me().then((pesponse) => {

        if (pesponse.data.resultCode === 0) {

            let { id, email, login } = pesponse.data.data
            dispatch(setUserData(id, email, login, true))
        }
    })


};
export const login = (email, password, rememberMe) => async(dispatch) => {

    //dispatch(stopSubmit("login", { _error: "Common error" }))

    const pesponse = await authAPI.login(email, password, rememberMe)

    if (pesponse.data.resultCode === 0) {
        dispatch(geAuthUserData())
    } else {
        let message = pesponse.message > 0 ? pesponse.message[0] : "Some error"
        dispatch(stopSubmit("login", { _error: `${message}` }))
    }

};
export const logout = () => async(dispatch) => {
    const pesponse = await authAPI.logout()
    if (pesponse.data.resultCode === 0) {
        dispatch(setUserData(null, null, null, false))
    }
};