import React, { Suspense } from "react";
import "./App.css";
import Navbar from "./components/Navbar/Navbar";
import { Route, withRouter } from "react-router-dom";
//import DialogsConteiner from "./components/Dialogs/DialogsConteiner";

//import UsersContainer from "./components/Users/UsersContainer";
//import Settings from "./components/Setings/Settings";
//import Music from "./components/Music/Music";
import ProfileContainer from "./components/Profile/ProfileContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
//import Login from "./components/Login/Login"
import { compose } from "redux";
import { connect, Provider } from "react-redux";
import { initializeApp } from "./redux/appReducer";
import Preloader from "./components/common/preloader/preloader";
import { BrowserRouter } from "react-router-dom/cjs/react-router-dom.min";
import store from "./redux/StoreRedux";
const  DialogsConteiner = React.lazy(()=> import ("./components/Dialogs/DialogsConteiner"));
const  Settings = React.lazy(()=> import ("./components/Setings/Settings"));
const  Music = React.lazy(()=> import ("./components/Music/Music"));
const  Login = React.lazy(()=> import ("./components/Login/Login"));
const  UsersContainer = React.lazy(()=> import ("./components/Users/UsersContainer"));



class App extends React.Component {

  componentDidMount(){
    
     this.props.initializeApp()
  }
  render(){
    
    if(!this.props.initialized) {
      return <Preloader/>
    }
  return (
    <div className="app_wrapper">
      <HeaderContainer />
      <Navbar />
      <div className="app_wrapper_content">
        <div>
        <Suspense fallback={<div>"Загрузка"</div>}> 
          <Route path="/dialogs" render={() => <DialogsConteiner />}/>{" "}
          <Route path="/profile:userId?" render={() => <ProfileContainer />} />{" "}
          <Route path="/users" render={() => <UsersContainer />} />{" "}
          <Route path="/settings" render={() => <Settings />} />{" "}
          <Route path="/music" render={() => <Music />} />{" "}
          <Route path="/login" render={() => <Login />} />{" "}
          </Suspense>
        </div>{" "}
        
      </div>{" "}
    

    </div>
  );
}};

const mapStateToProps = (state) => {
  return {
    initialized: state.app.initialized
  }
}
const AppConrainer = compose(
  withRouter,
  connect(mapStateToProps,{initializeApp})
)(App)

const SVLOEX = (props) => {
  return <BrowserRouter>
    <Provider store={store}>
      <AppConrainer />
    </Provider>{" "}
  </BrowserRouter>
}
export default SVLOEX
