import { usersAPI } from "../api/api";

const FOLOLOW = "FOLOLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const Current_Upd = "Current_Upd";
const TotalUsersCountUpd = "TotalUsersCountUpd";
const Toggle_is_fetch = "Toggle_is_fetch";
const Toggle_is_fetch_process = "Toggle_is_fetch_process";

let initialState = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLOLOW:
            return {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.userid) {
                        return {...u, followed: true };
                    }
                    return u;
                }),
            };
        case UNFOLLOW:
            return {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.userid) {
                        return {...u, followed: false };
                    }
                    return u;
                }),
            };
        case SET_USERS:
            return {...state, users: action.users };
        case Current_Upd:
            return {...state, currentPage: action.p };
        case TotalUsersCountUpd:
            return {...state, totalUsersCount: action.total };
        case Toggle_is_fetch:
            return {...state, isFetching: action.isFetching };
        case Toggle_is_fetch_process:
            return {
                ...state,
                followingInProgress: action.isFetching ? [...state.followingInProgress, action.userId] : state.followingInProgress.filter(id => id !== action.userId)
            };
        default:
            return state;
    }
};

export default usersReducer;
export const followUpd = (userid) => {
    return {
        type: FOLOLOW,
        userid,
    };
};
export const unfollowUpd = (userid) => {
    return {
        type: UNFOLLOW,
        userid,
    };
};
export const setUsersUpd = (users) => {
    return {
        type: SET_USERS,
        users,
    };
};
export const currentUpd = (p) => {
    return {
        type: Current_Upd,
        p,
    };
};
export const totalUsersCountUpd = (total) => {
    return {
        type: TotalUsersCountUpd,
        total: total
    };
};
export const toggleSet_is_fetchUpd = (isFetching) => {
    return {
        type: Toggle_is_fetch,
        isFetching
    };
};
export const toggleSet_is_fetch_processUpd = (isFetching, userId) => {
    return {
        type: Toggle_is_fetch_process,
        isFetching,
        userId
    };
};
export const requestUsers = (page, pageSize) => {
    return async(dispatch) => {
        dispatch(toggleSet_is_fetchUpd(true))
            //if (this.props.users.length === 0 | this.props.users.length > 0) {
        dispatch(currentUpd(page))
        const data = await usersAPI.getUsers(page, pageSize)


        dispatch(toggleSet_is_fetchUpd(false))
        dispatch(setUsersUpd(data.items))
        dispatch(totalUsersCountUpd(data.totalCount))

    }
};
export const follow = (userId) => {
    return async(dispatch) => {
        dispatch(toggleSet_is_fetch_processUpd(true, userId))
            //if (this.props.users.length === 0 | this.props.users.length > 0) {
        const pesponse = await usersAPI.follow(userId)

        if (pesponse.data.resultCode === 0) {
            dispatch(followUpd(userId))
        }
        dispatch(toggleSet_is_fetch_processUpd(false, userId))

    }
};
export const unfollow = (userId) => {
    return async(dispatch) => {
        dispatch(toggleSet_is_fetch_processUpd(true, userId))
            //if (this.props.users.length === 0 | this.props.users.length > 0) {
        const pesponse = await usersAPI.unfollow(userId)

        if (pesponse.data.resultCode === 0) {
            dispatch(unfollowUpd(userId))
        }
        dispatch(toggleSet_is_fetch_processUpd(false, userId))

    }
};