import React from "react";
import s from "./ProfileInfo.module.css";
//import userPhoto from "..//..//IMAGEsrc/madara.jpg";
import Preloader from "../../common/preloader/preloader";
//import ProfileStatus from "./ProfileStatus";
import StatusWithHook from "./ProfileStatusHook";

const ProfileInfo =React.memo( (props) => {
  console.log("render info")
  if(!props.profile){
    return <Preloader/>
  }
  return (
    <div>
        {/* <img
                  src={ userPhoto}
                  alt=""
                /> */}
      <div className={s.discriptionBlock}>
     <img src={props.profile.photos.large} alt="e" />
     <StatusWithHook status={props.status} updateStatus={props.updateStatus}/>
     
     <p>fullName: {props.profile.fullName}</p>
     <p>aboutMe: {props.profile.aboutMe}</p>
     { <a href={"https://" + (props.profile.contacts.vk)}>Vk: {props.profile.contacts.vk}</a>}
        </div>
        
    </div>
  );
});
export default ProfileInfo;
