const SEND_MESSAGE = "SEND_MESSAGE";
let initialState = {
    dialogs: [
        { id: 1, name: "loex" },
        { id: 2, name: "svloex" },
        { id: 3, name: "sveta" },
        { id: 4, name: "victa" },
        { id: 5, name: "lera" },
        { id: 6, name: "god" },
    ],
    messages: [
        { id: 1, message: "Hi" },
        { id: 2, message: "How is you loex?" },
        { id: 3, message: "Yo" },
    ],

};

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            let body = action.newMessageBody;
            return {
                ...state,

                messages: [...state.messages, { id: 4, message: body }],
            };

        default:
            return state;
    }
};
export default dialogsReducer;
export const sendMessageCreator = (newMessageBody) => {
    return {
        type: SEND_MESSAGE,
        newMessageBody
    };
};